m;\n            }\n            &.hidden {\n                display: none;\n            }\n        }\n        .map-icon {\n            svg {\n                width: 31px;\n                height: 32px;\n                @include breakpoint(phablet) {\n                    width: 36px;\n                    height: 37px;\n                }\n            }\n        }\n    }\n    .map-item-list-wrapper {\n        position: absolute;\n        .map-item-list-container {\n            @include border-radius(0 0 3px 3px);\n            @include transition(all 0.1s ease-in-out);\n            box-sizing: border-box;\n            overflow: hidden;\n            max-height: 0;\n            position: relative;\n            top: 10px;\n            background-color: #fbfcfd;\n            right: 205px;\n            width: 260px;\n            @include breakpoint(phablet) {\n                right: 245px;\n                width: 300px;\n            }\n            @media print {\n                display: none;\n            }\n            &.open {\n                @include box-shadow(\n                    0 5px 5px -3px rgba(0, 0, 0, 0.2),\n                    0 8px 10px 1px rgba(0, 0, 0, 0.14),\n                    0 3px 14px 2px rgba(0, 0, 0, 0.12)\n                );\n                height: auto;\n                max-height: 300px;\n                overflow: auto;\n            }\n            .page-link-element {\n                display: block;\n                text-align: center;\n                padding: 8px 16px;\n                color: $primary-text;\n                text-decoration: none;\n                cursor: pointer;\n                border: none;\n                font-size: 16px;    \n                font-weight: 400;\n                width: 100%;\n                font-family: $default-font;\n                &:hover {\n                    text-decoration: underline;\n                    color: $primary-text-hover;\n                }\n            }\n            ul {\n            